import { mapGetters } from 'vuex'
import ScrollFix from '@/vendor/ScrollFix'
import LazyLoad from 'vanilla-lazyload'
import { getAgentCode } from '@/api/'
import { delQueStr } from '@/utils/utils'
export default {
  data () {
    return {
      scrollFix: null,
      code: ''
    }
  },
  beforeDestroy () {
    if (this.scrollFix) {
      this.scrollFix.destory()
    }
    this.lazyload && this.lazyload.destroy()
  },
  beforeRouteEnter (to, from, next) {
    if (Number(to.query.type) === 1) {
      document.title = '商户加盟'
    } else if (Number(to.query.type) === 2) {
      document.title = '代理加盟'
    }
    next()
  },
  created () {
    var codeStr = ''
    if (this.$route.query.code) {
      this.code = this.$route.query.code
    }
    getAgentCode().then((res) => {
      var code = ''
      if (res.result) {
        code = res.result
      } else if (!res.result && this.code) {
        code = this.code
      }
      codeStr = '&code=' + code
      var shareParams = {}
      if (Number(this.$route.query.type) === 1) {
        shareParams = {
          title: '商户加盟：0成本增加收益，微信注册即可免费使用',
          desc: '该系统适用于酒吧、庆典、餐厅、婚礼等各种场合，帮您活跃气氛的同时提升30%以上收益',
          link: window.location.origin + window.location.pathname + `#/BusinessJoin?type=${this.$route.query.type}` + codeStr,
          imgUrl: 'http://xnb.siweiquanjing.com/screen/images/logo1.png' // 分享图标
        }
      } else if (Number(this.$route.query.type) === 2) {
        shareParams = {
          title: '代理加盟：推荐商户免费使用牛霸系统获永久收益分成',
          desc: '该系统适用于酒吧、庆典、餐厅、婚礼等各种场合',
          link: delQueStr(window.location.href, 'version'),
          imgUrl: 'http://xnb.siweiquanjing.com/screen/images/logo1.png' // 分享图标
        }
      }
      this.$wechat.onMenuShareTimeline({
        ...shareParams,
        success: function () {},
        cancel: function () {}
      })
      this.$wechat.onMenuShareAppMessage({
        ...shareParams,
        success: function () {},
        cancel: function () {}
      })
    })
  },
  mounted () {
    this.$nextTick(() => {
      this.lazyload = new LazyLoad({
        container: this.$refs.scrollWrapper
      })
      this.scrollFix = new ScrollFix(this.$refs.scrollWrapper)
    })
  },
  computed: {
    ...mapGetters('user', {
      userInfo: 'userInfo'
    })
  }
}



